import { Link } from 'react-router-dom'
import useWallet from '@/lib/hooks/useWallet'

export default function Header() {
  const { account, isConnected, connect, disconnect } = useWallet()

  const shortAddress = (addr: string) =>
    addr ? `${addr.slice(0, 6)}...${addr.slice(-4)}` : ''

  const handleConnectWallet = async () => {
    if (isConnected) {
      disconnect()
      return
    }
    await connect()
  }

  return (
    <header className="fixed top-0 left-0 right-0 z-50 bg-indigo-950/80 backdrop-blur-md border-b border-white/10">
      <div className="container mx-auto px-4">
        <div className="flex items-center justify-between h-16">
          {/* Logo/Brand - Left */}
          <Link to="/" className="flex items-center space-x-2 group">
            <div className="w-8 h-8 bg-gradient-to-br from-blue-400 to-purple-500 rounded-lg flex items-center justify-center group-hover:scale-110 transition-transform">
              <span className="text-white font-bold text-lg">W3</span>
            </div>
            <span className="text-white font-bold text-xl hidden sm:block">
              Web3 DApp
            </span>
          </Link>

          {/* Navigation - Center */}
          <nav className="absolute left-1/2 -translate-x-1/2">
            <Link
              to="/dapp"
              className="px-6 py-2 text-white/90 hover:text-white font-medium rounded-lg hover:bg-white/10 transition-all duration-200"
            >
              Launch DApp
            </Link>
          </nav>

          {/* Wallet - Right */}
          {isConnected ? (
            <div className="flex items-center gap-3">
              <span className="px-3 py-1 rounded-md bg-white/10 text-white/90 font-mono text-sm">
                {shortAddress(account)}
              </span>
              <button
                onClick={disconnect}
                className="px-3 py-2 bg-white/10 hover:bg-white/20 text-white font-medium rounded-md border border-white/10 transition-colors"
              >
                Disconnect
              </button>
            </div>
          ) : (
            <button
              onClick={handleConnectWallet}
              className="px-6 py-2.5 bg-gradient-to-r from-blue-500 to-purple-600 hover:from-blue-600 hover:to-purple-700 text-white font-semibold rounded-lg shadow-lg hover:shadow-xl transform hover:scale-105 transition-all duration-200 flex items-center gap-2 cursor-pointer"
            >
              <svg
                className="w-5 h-5"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M17 9V7a2 2 0 00-2-2H5a2 2 0 00-2 2v6a2 2 0 002 2h2m2 4h10a2 2 0 002-2v-6a2 2 0 00-2-2H9a2 2 0 00-2 2v6a2 2 0 002 2zm7-5a2 2 0 11-4 0 2 2 0 014 0z"
                />
              </svg>
              <span className="hidden sm:inline">Connect Wallet</span>
              <span className="sm:hidden">Connect</span>
            </button>
          )}
        </div>
      </div>
    </header>
  )
}
